import { Box } from '@mui/material';
import CardProject from '../components/CardProject';
import LYKImg from '../assets/images/lyk-brewing-co_cap2.png';
import HGImg from '../assets/images/hangman_cap2.png';
import PFImg from '../assets/images/portfolio_cap2.png';

function Projects() {

    const projects = [
        {
            name: 'LYK BREWING CO',
            description: 'React, JavaScript & Tailwind-CSS',
            image: LYKImg,
            code: 'https://github.com/pauair/lyk-brewing-co',
            visit: 'https://pauair.github.io/lyk-brewing-co/',
        },
        {
            name: 'HANGMAN GAME',
            description: 'React, JavaScript & Tailwind-CSS',
            image: HGImg,
            code: 'https://github.com/pauair/hangman-game',
            visit: 'https://pauair.github.io/hangman-game/',
        },
        {
            name: 'PORTFOLIO ',
            description: 'React, TypeScript, Material UI & CSS',
            image: PFImg,
            code: 'https://github.com/pauair/portfolio',
            visit: 'https://pauair.github.io/portfolio/',
        }
    ]

    return (
        <Box id="projects" minWidth={'80vw'} sx={{ margin: '10vw', display: 'flex', flexDirection: 'row', flexWrap: 'wrap', justifyContent: 'center', alignItems: 'center' }}>
            {projects.map((project, index) => (
                <CardProject key={index} name={project.name} description={project.description} image_url={project.image} code_url={project.code} visit_url={project.visit}/>
            ))}
        </Box>
    );
}

export default Projects;